.App {
  height: 400px;
  display: flex;
  align-items: center;
}

.block {
  position: relative;
  height: 250px;
  width: 300px;
  overflow-y: scroll;
  background-color: #6f6eb3;
  border-radius: 10px;
}

.block::-webkit-scrollbar {
  width: 10px;
}

.block::-webkit-scrollbar-track {
  border-radius: 10px;
}

.block::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 1);
  border-radius: 10px;
}

.block::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}
